html {
    height: 100%;
    font-family: Frutiger;
}

body {
    background: linear-gradient(#00284a, #003d6e);
    color: white;
}

button:focus {
    outline: none;
}

#container {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#quote-box {
    text-align: right;
    border: 2px solid #cccc00;
    padding: 20px 40px;
    font-size: 40px;
    border-radius: 10px;
}

#text {
    text-align: center;
    animation-name: fade;
    animation-duration: .5s;
    animation-timing-function: ease-out;
}

#author {
    text-align: right;
    font-family: Helvetica;
    font-size: 20px;
    animation-name: fade;
    animation-duration: .5s;
    animation-timing-function: ease-out;
}

#tweet-quote {
    float: left
}

#tweet-quote-btn {
    color: #00284a;
    background: white;
    transition: 0.3s;
    padding: 7px 10px;
    border-radius: 7px;
    border: 0;
    font-size: 24px;
}

#tweet-quote-btn:hover {
    background: #cccccc;
}

button {
    outline: none;
}

#new-quote {
    color: #00284a;
    background: white;
    transition: 0.3s;
    padding: 7px 10px;
    border-radius: 7px;
    border: 0;
    font-size: 18px;
    font-family: Helvetica;
}

#new-quote:hover {
    background: #cccccc;
}

.footer {
    text-align: center;
}

#footer {
    font-family: Arial;
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@media only screen and (max-width: 800px) {
    #container {
        position: absolute;
        top: 50%;
        left: 25%;
        transform: translate(-17%, -50%);
    }

    #text {
        font-size: 32px;
    }

    #auth {
        font-size: 17px;
    }

    #tweet-quote-btn {
        font-size: 16px;
    }

    #new-quote {
        font-size: 16px;
    }
}

@media only screen and (max-width: 580px) {
    #container {
        position: absolute;
        top: 50%;
        left: 15%;
        transform: translate(-8.6%, -50%);
    }

    #text {
        font-size: 25px;
    }

    #auth {
        font-size: 13px;
    }
}
